/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*
	SVG Loader by Aurer:
	https://codepen.io/aurer/pen/jEGbA
*/
*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: #454545;
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

header {
  padding: 10px 40px;
  background-color: #FEE298;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.scroll {
  position: fixed;
  top: 38px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
}

form {
  padding: 20px 40px 0;
}

form ul {
  list-style-type: none;
}

form ul li {
  padding-bottom: 10px;
}

form ul li label {
  display: block;
  float: left;
  width: 80px;
  text-align: right;
  padding-right: 10px;
  font-size: 13px;
  line-height: 28px;
}

form ul li label::after {
  content: ':';
}

form ul li input,
form ul li textarea {
  display: block;
  padding-left: 10px;
  padding-right: 28px;
  width: calc(100% - 80px);
  border: 1px solid #cccccc;
  background-color: #fff;
  background-position: right 6px top 6px;
  background-repeat: no-repeat;
  transition: background-color .15s;
}

form ul li input[required]:invalid:not(:placeholder-shown),
form ul li textarea[required]:invalid:not(:placeholder-shown) {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14"><path fill="#e14540" fill-rule="evenodd" d="M7,14 C3.13400675,14 0,10.8659932 0,7 C0,3.13400675 3.13400675,0 7,0 C10.8659932,0 14,3.13400675 14,7 C14,10.8659932 10.8659932,14 7,14 Z M8.41421356,7 L10.5355339,4.87867966 L9.12132034,3.46446609 L7,5.58578644 L4.87867966,3.46446609 L3.46446609,4.87867966 L5.58578644,7 L3.46446609,9.12132034 L4.87867966,10.5355339 L7,8.41421356 L9.12132034,10.5355339 L10.5355339,9.12132034 L8.41421356,7 Z"/></svg>');
}

form ul li input[required]:valid,
form ul li textarea[required]:valid {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14"><path fill="#37ca41" fill-rule="evenodd" d="M675,766 C671.134007,766 668,762.865993 668,759 C668,755.134007 671.134007,752 675,752 C678.865993,752 682,755.134007 682,759 C682,762.865993 678.865993,766 675,766 Z M674.035534,762.571068 L679.692388,756.914214 L678.278175,755.5 L674.035534,759.742641 L671.914214,757.62132 L670.5,759.035534 L674.035534,762.571068 Z" transform="translate(-668 -752)"/></svg>');
}

form ul li input {
  height: 28px;
}

form ul li textarea {
  height: 44px;
  resize: none;
}

footer {
  text-align: right;
  padding: 15px 0 0;
}

footer button {
  padding: 3px 14px;
  border: 1px solid #C5C3C5;
  border-bottom-color: #9F9E9F;
  background: #fff;
  font-size: 13px;
  border-radius: 5px;
}

footer button:active {
  color: #fff;
  border-color: #237DFE;
  border-bottom-color: #0240DD;
  background-image: linear-gradient(-180deg, #4A97FD 0%, #0968E5 100%);
}
